import React, { useCallback, useEffect, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { connect, useDispatch } from 'react-redux';
import { first, take } from 'rxjs/operators';
import { add, reset2, UploadIcon } from '../../../assets/images/icons';
import { setAuthenticatedUser } from '../../../reduxStore/actions/authActions';
import { updateUserById, updateUserProfilePicture, changeUsersPassword, removeUserbyId, updateUserOptionsById, getAuthenticatedUser } from '../../../services/user.service';
import { APP_NAME, APPEND_TO_DATA, dashboard_page_default, uncachedUrl } from '../../../shared/utility';
import { Button, ModalR, SelectWithSearch, UnfocusableWrapper } from '../../common';
import HeaderTopBar from '../../common/HeaderTopBar/HeaderTopBar';
import cls from './MyProfile.module.scss';
import { isMobile } from 'react-device-detect';
import { setShowMenu, setShowSideMenu } from '../../../reduxStore/actions/flowActions';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import { sessionService } from 'redux-react-session';
import UserOverviewModal from '../../SideMenuEntries/CompanyComps/UserOverviewModal';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { addUserToPosition, removeUserFromPosition } from '../../../services/company.service';
import * as R from 'ramda';
import { getAlComponentsByModuleId } from '../../../services/module.service';
import moment from 'moment';
import { PHOTO_URL } from '../../../services/API_CONSTANTS';


const MyProfile = props => {
	const { authenticatedUser, user, components, companyData, companyPositionsArray } = props;

	const location = useLocation();
	const data = location.state;

	const [avatarError, setAvatarError] = useState('');
	const [authenticUser, setAuthenticUser] = useState(data?.user ?? authenticatedUser);

	const [actualPassword, setActualPassword] = useState('');
	const [actualPasswordError, setActualPasswordError] = useState('');
	const [mobile, setMobile] = useState(authenticUser.phone);
	const [newPassword, setNewPassword] = useState('');
	const [confirmNewPassword, setConfirmNewPassword] = useState('');
	const [confirmationError, setConfirmationError] = useState('');
	const [deleteUser, setDeleteUser] = useState(false);

	const [loading, setLoading] = useState(false);
	const [showTasks, setShowTasks] = useState(false);
	const [showResetSuccessModal, setShowResetSuccessModal] = useState(false);
	const [showBio, setShowBio] = useState(false);
	const [newField, setNewField] = useState(false);

	document.title = `My Profile | ${APP_NAME}`;
	const navigate = useNavigate();

	const dispatch = useDispatch();

	const onDrop = useCallback(
		acceptedFiles =>
			acceptedFiles.forEach(file => {
				setAvatarError('');
				if (file?.type?.toString().indexOf('image/') > -1 && ['jpeg', 'jpg', 'png'].indexOf(file?.type?.toString().split('/')[1]) > -1) {
					let reader = new FileReader();
					reader.readAsDataURL(file);
					reader.onloadend = _e => {
						const formData = new FormData();
						formData.append('avatar', file);
						updateUserProfilePicture(authenticUser.id, file).subscribe({
							next: data => {
								setAuthenticUser(data);
							},
							error: error => {
								setAvatarError(error?.data);
							},
						});
					};
				}
			}),
		[]
	);
	const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

	const [authenticUser_ApiError, setAuthenticUser_ApiError] = useState('');
	const [updateUser_apiError, setUpdateUser_ApiError] = useState('');

	useEffect(() => {
		setAuthenticUser_ApiError('');

		if (data?.user?.id.length > 0) {
			setAuthenticUser(data.user);
			dispatch(setShowMenu(false));
		} else {
			getAuthenticatedUser()
				.pipe(first())
				.subscribe({
					next: data => {
						setAuthenticUser(data);
						authenticatedUser.wallet = data?.wallet;
						authenticatedUser.options = data?.options;
						authenticatedUser.departments = data?.departments;
						authenticatedUser.positions = data?.positions;
						authenticatedUser.pinned_modules = data?.pinned_modules;
						sessionService?.saveUser?.(data).then(() => {
						});
					},
				});
		}

		dispatch(setShowSideMenu(false));
	}, []);

	const [fieldsErrors, setFieldErrors] = useState({ first_name: '', last_name: '', mobile: '', address: '', city: '', postal: '' });
	const [elementsTouched, setElementsTouched] = useState({ first_name: false, last_name: false, mobile: false, address: false, city: false, postal: false });

	const onSaveUserData = useCallback(() => {
		if (!Object.values(elementsTouched).some(value => value === true)) {
			return;
		}
		setFieldErrors({ first_name: '', last_name: '', mobile: '', address: '', city: '', postal: '' });
		setUpdateUser_ApiError('');


		updateUserById(authenticUser.id, authenticUser.first_name, authenticUser.last_name, authenticUser.phone, authenticUser.address, authenticUser.city, authenticUser.postal)
			.pipe(first())
			.subscribe({
				next: data => {
					if (!data?.user) {
						authenticatedUser.first_name = data?.first_name;
						authenticatedUser.last_name = data?.last_name;
						authenticatedUser.phone = data?.phone;
						authenticatedUser.postal = data?.postal;
						authenticatedUser.address = data?.address;
						authenticatedUser.city = data?.city;
						authenticUser.photo = data?.photo;
					}

					setElementsTouched({ first_name: false, last_name: false, mobile: false, address: false, city: false, postal: false });
				},
				complete: () => window.scrollTo({ top: 0, behavior: 'smooth' }),
				error: error => {
					const foundErrors = { first_name: '', last_name: '', mobile: '', address: '', city: '', postal: '' };
					if (error.data.errors.first_name) {
						foundErrors.first_name = error.data.errors.first_name?.join('; ');
					}
					if (error.data.errors.last_name) {
						foundErrors.last_name = error.data.errors.last_name?.join('; ');
					}
					if (error.data.errors.mobile) {
						foundErrors.mobile = error.data.errors.mobile?.join('; ');
					}
					if (error.data.errors.address) {
						foundErrors.address = error.data.errors.address?.join('; ');
					}
					if (error.data.errors.city) {
						foundErrors.city = error.data.errors.city?.join('; ');
					}
					if (error.data.errors.postal) {
						foundErrors.postal = error.data.errors.postal?.join('; ');
					}
					if (error.data.message !== 'The given data was invalid.') {
						setUpdateUser_ApiError(error.data.message);
					}
					setFieldErrors(c => ({ ...c, ...foundErrors }));
				},
			});
	}, [authenticUser, elementsTouched]);
	const renderDropZone = useCallback(() => {
		return (
			<div className={cls.avatarDragContainer} app-drag-active={isDragActive ? 'true' : 'false'} {...getRootProps()}>
				<input {...getInputProps()} className={cls.avatarDragInput} accept="*,image/png, image/jpeg, image/jpg" />
				{isDragActive ? (
					<div className={cls.avatarContentContainer}>
						<UploadIcon className={cls.avatarUploadIcon} />
					</div>
				) : (
					<div className={cls.avatarContentContainer}>
						{authenticUser?.photo?.length > 2 ? (
							<img alt="lg" src={authenticUser?.photo} className={cls.avatarUrlImg} />
						) : (
							<div className={cls.avatarUploadPictureContainer}>
								<UploadIcon className={cls.avatarUploadIcon} />
							</div>
						)}
					</div>
				)}
			</div>
		);
	}, [authenticUser]);

	const onSubmitClick = () => {
		setLoading(true);
		if (newPassword !== confirmNewPassword) {
			setLoading(false);
			return setConfirmationError('Confirmation does not match');
		}
		changeUsersPassword(actualPassword, newPassword, confirmNewPassword, authenticUser.id)
			.pipe(first())
			.subscribe({
				next: () => {
					setLoading(false);
					setShowResetSuccessModal(true);
					setActualPasswordError('');
					setConfirmationError('');
					updateUser_apiError('');
					authenticUser_ApiError('');
				},
				error: error => {
					console.log(error);
					if (error?.data?.errors?.old_password) {
						setActualPasswordError(error?.data?.errors?.old_password[0]);
					}
					setLoading(false);
					setShowResetSuccessModal(false);
				},
				complete: () => {
					setActualPassword('');
					setNewPassword('');
					setConfirmNewPassword('');
				},
			});
	};

	const onDeleteUserClick = () => {
		setLoading(true);

		removeUserbyId(authenticUser.id)
			.pipe(first())
			.subscribe({
				next: () => {
					setLoading(false);
					setDeleteUser(false);
					navigate('/users');
				},
				error: error => {
					console.log(error);
				},
				complete: () => { },
			});
	};
	return (
		<div id="my-profile-settings" className={cls.host} style={{ padding: isMobile ? '1px' : '', margin: isMobile ? '0px' : '' }}>
			{data?.user?.id?.length > 0 && authenticatedUser.is_admin == true ? (
				<div style={{ width: '100%', display: 'flex', justifyContent: 'space-between', margin: '50px 0' }}>
					<Button
						titleStyle={{ fontWeight: 'normal' }}
						title={'Back'} clicked={() => navigate('/users')} style={{ width: '100px' }} />
					{data?.user && <Button
						titleStyle={{ fontWeight: 'normal' }}
						style={{ backgroundColor: 'var(--app-color-blue)' }}
						title="Show Tasks" loading={loading} clicked={() => setShowTasks(true)} />}
				</div>
			) : (
				!isMobile && <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between', margin: '50px 0' }}>
					<div style={{ margin: '200px 0 0 0', display: isMobile ? 'none' : '' }}>
						<HeaderTopBar headerTitle={'My Profile'} targetProcessing={false} />
					</div>
					<div style={{ display: '', alignSelf: 'center', height: 'auto', padding: '10px', textAlign: 'center', margin: '10px', fontFamily: 'var(--app-text-main-font)' }}>
						<LabeledField label={''}>
							{renderDropZone()}
							{avatarError?.length > 0 && <p className={cls.avatarErrorText}>{avatarError}</p>}
							<p style={{ fontSize: '16px' }}>
								{`ID: ${authenticUser.id.replace(/\D/g, '').slice(-5)}`}
							</p>
						</LabeledField>
					</div>
					<div />
				</div>
			)}
			
				{isMobile && (
					<div style={{  alignSelf: 'center', height: 'auto', padding: '10px', textAlign: 'center', fontFamily: 'var(--app-text-main-font)' }}>
						<LabeledField label={''}>
							{renderDropZone()}
							{avatarError?.length > 0 && <p className={cls.avatarErrorText}>{avatarError}</p>}

						</LabeledField>
					</div>
				)}
			

			<div style={{ display: 'flex', width: '100%', flexDirection: 'column' }}>
				<Tabs
					style={{ fontFamily: 'var(--app-text-main-font)', fontSize: isMobile ? '18px' : '20px' }}
				>
					{isMobile ? <TabList>
						<Tab>{'Basic'}</Tab>
						<Tab>{'Other'}</Tab>
						<Tab>{'Password'}</Tab>
					</TabList> :
						<TabList>
							<Tab>
								<p>
									{'Personal Information'}
								</p>
							</Tab>
							{companyData?.misc_info?.employee && <Tab>
								<p>
									{'Linked Information'}
								</p>
							</Tab>}
							<Tab>
								<p>
									{'Additional Information'}
								</p>
							</Tab>

							<Tab>
								<p>
									{'Change Password'}
								</p>
							</Tab>
						</TabList>}
					<TabPanel>
						<div className={cls.labeledContainerHost} style={{ width: isMobile ? '100%' : '50%', margin: '50px 0 100px' }}>

							<div className={cls.userDataWrapper} style={{ textAlign: 'center' }}>
								<UnfocusableWrapper className={cls.userSettingsContainer} style={{ paddingTop: '10px' }} onClickOutside={onSaveUserData}>
									<div style={{ width: 'inherit', textAlign: 'center' }}>
										<LabeledField label={'First Name'} error={fieldsErrors.first_name}>
											<input
												type="text"
												name="first-name"
												className={cls.inputField}
												placeholder="First Name"
												value={authenticUser?.first_name ?? ''}
												onChange={e => setAuthenticUser(c => ({ ...c, first_name: e.target.value }))}
												onFocus={() => {
													setElementsTouched(c => ({ ...c, display_name: true }));
												}}
											/>
										</LabeledField>
										<LabeledField label={'Last Name'} error={fieldsErrors.last_name}>
											<input
												type="text"
												name="last-name"
												className={cls.inputField}
												placeholder="Last Name"
												value={authenticUser?.last_name ?? ''}
												onChange={e => setAuthenticUser(c => ({ ...c, last_name: e.target.value }))}
												onFocus={() => {
													setElementsTouched(c => ({ ...c, display_name: true }));
												}}
											/>
										</LabeledField>
										{authenticUser?.username && <LabeledField label={'Username'} wrappedContent>
											<p className={cls.emailParagraph}
											>{authenticUser?.username}</p>
										</LabeledField>}
										<LabeledField label={'Work Email'} wrappedContent>
											<p className={cls.emailParagraph}
											>{authenticUser?.email}</p>
										</LabeledField>

										<LabeledField label={'Mobile Number'} error={fieldsErrors.mobile}>
											<input
												type="text"
												name="mobile"
												className={cls.inputField}
												value={authenticUser?.phone}
												onChange={e => setAuthenticUser(c => ({ ...c, phone: e.target.value }))}
												onFocus={() => {
													setElementsTouched(c => ({ ...c, display_name: false, mobile: true }));
												}}
											/>
										</LabeledField>
										<LabeledField label={'Address'} error={fieldsErrors.address}>
											<input
												type="text"
												name="address"
												className={cls.inputField}
												value={authenticUser?.address}
												onChange={e => setAuthenticUser(c => ({ ...c, address: e.target.value }))}
												onFocus={() => {
													setElementsTouched(c => ({ ...c, display_name: false, mobile: false, address: true, city: false, postal: false }));
												}}
											/>
										</LabeledField>
										<LabeledField label={'City'} error={fieldsErrors.city}>
											<input
												type="text"
												name="city"
												className={cls.inputField}
												value={authenticUser?.city}
												onChange={e => setAuthenticUser(c => ({ ...c, city: e.target.value }))}
												onFocus={() => {
													setElementsTouched(c => ({ ...c, display_name: false, mobile: false, address: false, city: true, postal: false }));
												}}
											/>
										</LabeledField>
										<LabeledField label={'Postal'} error={fieldsErrors.postal}>
											<input
												type="text"
												name="postal"
												className={cls.inputField}
												value={authenticUser?.postal}
												onChange={e => setAuthenticUser(c => ({ ...c, postal: e.target.value }))}
												onFocus={() => {
													setElementsTouched(c => ({ ...c, display_name: false, mobile: false, address: false, city: false, postal: true }));
												}}
											/>
										</LabeledField>

										{companyData.misc_info?.positions && <LabeledField label={companyData?.misc_info?.positionsL ?? 'Position'} error={fieldsErrors.postal}>

											<SelectWithSearch
												id={'status-selection'}
												triggerType={'chevron'}
												itemType={'no-item-icons'}
												className={cls.inputField}
												inputClassName={cls.selectText}
												molclassname={cls.industryMOL}
												moliclassname={cls.industryMOLI}
												onChange={val => {
													updateUserOptionsById(authenticUser, { ...authenticUser?.options, selPos: val.value })
														.pipe(first())
														.subscribe({
															next: data => {

																addUserToPosition(
																	val.value,
																	authenticUser?.id
																).subscribe({
																	next: () => {

																		if (authenticUser.options.selPos && authenticUser.options.selPos !== val?.value) {
																			// remove previous position
																			removeUserFromPosition(
																				authenticUser.options.selPos,
																				authenticUser?.id
																			).subscribe({
																				next: () => {
																					window.scrollTo({ top: 0, behavior: 'smooth' });
																				}
																			});

																		} else {
																			window.scrollTo({ top: 0, behavior: 'smooth' });
																		}

																		setAuthenticUser({ ...authenticUser, options: { ...authenticUser?.options, selPos: data?.options?.selPos } })

																		if (!data?.user) {
																			authenticatedUser.options.selPos = data?.options?.selPos;
																		}
																	},
																});
															}

														});
												}}
												defaultSelectedItem={companyPositionsArray?.find(p => p?.value === authenticUser?.options?.selPos)}
												items={companyData.misc_info?.positions?.map(po => {
													const pos = companyPositionsArray?.find(p => p?.value === po);

													return { label: pos?.label, value: pos?.value }
												})}
											/>

										</LabeledField>}
										{user.is_admin == true && <LabeledField label={'Position' + (authenticUser.positions?.length > 1 ? 's' : '')} wrappedContent>
											{authenticUser.positions?.length > 0 &&
												authenticUser.positions?.map((i, idx) => (
													<p key={idx} className={cls.positionsParagraph}>
														{i.name}
													</p>
												))}
										</LabeledField>}
										<br />
									</div>


								</UnfocusableWrapper>

							</div>
						</div>
					</TabPanel>

					{companyData?.misc_info?.employee && !isMobile && <TabPanel>
						<div style={{ width: isMobile ? '100%' : '50%', margin: '80px 0 100px' }}>
							<div style={{ display: 'flex', justifyContent: 'right', height: isMobile ? '0px' : '40px' }}>
								<Button
									loading={loading}
									light
									clicked={() => {
										setLoading(true);
										getAlComponentsByModuleId({ id: companyData?.misc_info?.employee }, APPEND_TO_DATA, dashboard_page_default, 1)
											.pipe(take(1))
											.subscribe({
												next: () => setLoading(false)
											})
									}}
									icon={<img src={reset2} />}
								/>
							</div>
							<div className={cls.userDataWrapper}>
								<UnfocusableWrapper className={cls.userSettingsContainer} style={{ padding: isMobile ? '0' : '' }} >

									{R.sortWith([R.descend(R.prop('group'))])(components.find(co => co.module_id === companyData.misc_info?.employee && co.assigned_to?.id === authenticUser?.id)?.component_fields?.filter(cf => cf.data_type_name !== 'Pin' && !['First Name', 'Middle Name', 'Last Name', 'Working Email', 'Mobile Number', 'City', 'Postal'].find(l => cf.field_name === l)) ?? []).map((cf, i) => {
										return <LabeledField key={i} label={cf?.field_name} >
											<input
												className={cls.inputField}
												value={cf.data_type_name === 'Date' ? moment(new Date(cf?.value)).format('DD MMM YYYY') : cf?.value}
											/>
										</LabeledField>
									})}
								</UnfocusableWrapper>
							</div>


						</div>
					</TabPanel>}
					<TabPanel>
						<div style={{ width: isMobile ? '100%' : '50%', margin: '80px 0 100px' }}>
							<div style={{ display: 'flex', justifyContent: 'right', height: isMobile ? '0px' : '40px' }}>

								{!isMobile && !newField && <Button
									light
									clicked={() => {
										setNewField(authenticUser.options?.add_info ?? {})
									}}
									icon={<img src={add} />}
								/>}



							</div>
							<div className={cls.userDataWrapper}>
								<UnfocusableWrapper className={cls.userSettingsContainer} style={{ padding: isMobile ? '0' : '' }} >
									<LabeledField label={'About Me'} error={fieldsErrors.address}>
										<textarea
											type="text"
											name="bio"
											className={cls.inputField}
											style={{ minHeight: '160px', textAlign: isMobile ? 'left' : 'left', padding: '5px' }}
											value={authenticUser?.options?.info}
											onChange={e => setAuthenticUser(c => ({ ...c, options: { ...authenticUser?.options, info: e.target.value } }))}
											onBlur={() => {
												updateUserOptionsById(authenticUser, { ...authenticUser?.options, info: authenticUser?.options?.info })
													.pipe(first())
													.subscribe({
														next: data => {
															window.scrollTo({ top: 0, behavior: 'smooth' });
															setAuthenticUser({ ...authenticUser, options: { ...authenticUser?.options, info: data?.options?.info } })
															if (!data?.user) {
																authenticatedUser.options.info = data?.options?.info;
															}
														}

													});
											}}

										/>
									</LabeledField>




									{Object.keys(authenticUser.options?.add_info ?? {}).map((label, i) => {
										return <LabeledField label={label} key={i} error={fieldsErrors.address}>
											<input
												type="text"
												name={label}
												className={cls.inputField}
												value={authenticUser?.options?.add_info[label]}
												onChange={e => setAuthenticUser({ ...authenticUser, options: { ...authenticUser?.options, add_info: { ...authenticUser?.options?.add_info, [label]: e.target.value } } })}
												onBlur={() => {
													updateUserOptionsById(authenticUser, { ...authenticUser?.options, add_info: authenticUser?.options?.add_info })
														.pipe(first())
														.subscribe({
															next: data => {
																window.scrollTo({ top: 0, behavior: 'smooth' });
																setAuthenticUser({ ...authenticUser, options: { ...authenticUser?.options, add_info: data?.options?.add_info } })
																if (data?.user) {
																	authenticatedUser.options.add_info = data?.options?.add_info;
																}
															}

														});
												}}
											/>
										</LabeledField>
									})}
									{newField && <LabeledField label={'New Field'} >
										<input
											type="text"
											id="newfield"
											className={cls.inputField}

										/>
									</LabeledField>}

									{/* {!authenticUser?.photo?.includes(PHOTO_URL) && <LabeledField label={'Profile Picture'} error={fieldsErrors.address}>

										<textarea
											type="text"
											name="bio"
											className={cls.inputField}
											style={{ minHeight: '160px', textAlign: isMobile ? 'left' : 'left', padding: '5px' }}
											defaultValue={authenticUser?.photo}
											onBlur={e => {
												setAuthenticUser(c => ({ ...c, photo: e.target.value }))
											}}

										/>

									</LabeledField>} */}

									{newField &&
										<div style={{ display: 'flex', justifyContent: 'right', height: '70px' }}>
											<Button title={'Add'}
												clicked={() => {
													newField[document.getElementById('newfield').value] = '';
													setAuthenticUser({ ...authenticUser, options: { ...authenticUser?.options, add_info: newField } })
													setNewField(false);
												}} />
											<Button light title={'Cancel'}
												clicked={() => {

													setNewField(false);
												}} />
										</div>
									}

								</UnfocusableWrapper>
							</div>


						</div>
					</TabPanel>
					<TabPanel>
						<div style={{ width: isMobile ? '100%' : '50%', margin: '80px 0 100px 0' }}>
							{/* <LabeledContainer label={'Change Password'} /> */}
							<div style={{ display: isMobile ? '' : 'flex', gap: '24px', marginTop: '15px', width: '100%' }}>

								<div style={{ flex: 1 }}>
									<LabeledField label={data?.user?.id?.length > 0 && authenticatedUser.is_admin == true ? 'Admin Password' : 'Old Password'} error={fieldsErrors.first_name}>
										<input type="password" name="old-password" className={cls.inputField}
											value={actualPassword} onChange={e => setActualPassword(e.target.value)} />
									</LabeledField>
								</div>
								<div style={{ flex: 1 }}>
									<LabeledField label={'New Password'} error={fieldsErrors.first_name}>
										<input type="password" name="new-password"
											className={cls.inputField}
											value={newPassword} onChange={e => setNewPassword(e.target.value)} />
									</LabeledField>
								</div>
								<div style={{ flex: 1 }}>
									<LabeledField label={'Verify New Password'} error={fieldsErrors.last_name}>
										<input type="password" name="verify-password"
											className={cls.inputField}
											value={confirmNewPassword} onChange={e => setConfirmNewPassword(e.target.value)} />
									</LabeledField>
								</div>
							</div>

							<div style={{ display: 'flex', width: isMobile ? '100%' : '50%', justifyContent: isMobile ? 'center' : 'left' }}>
								{actualPassword.length > 5 && newPassword.length > 5 && confirmNewPassword.length > 5 && (
									<div style={{ display: 'flex', gap: '24px', marginTop: '15px', maxWidth: '100%' }}>
										<Button title="Update password" loading={loading} lighter clicked={onSubmitClick} />
									</div>
								)}
								{user.is_admin == true && data !== null && data?.user.is_admin == false && data?.user.positions.length == 0 && components.filter(component => component?.created_by?.id === data?.user?.id).length === 0 && (
									<div style={{ display: 'flex', gap: '24px', marginTop: '15px', maxWidth: '100%' }}>
										<Button title="Delete User" loading={loading} light clicked={() => setDeleteUser(true)} />
									</div>
								)}

							</div>
							<div style={{ width: '100%', display: 'flex', justifyContent: isMobile ? 'center' : 'left' }}>
								<div>{showResetSuccessModal && <p className={cls.success}>Password Successfully changed</p>}</div>
								<div>{actualPasswordError?.length > 0 && <p className={cls.errorGeneralText}>{actualPasswordError}</p>}</div>
								<div>{confirmationError?.length > 0 && <p className={cls.errorGeneralText}>{confirmationError}</p>}</div>
								<div>{authenticUser_ApiError?.length > 0 && <p className={cls.errorGeneralText}>{authenticUser_ApiError}</p>}</div>
								<div>{updateUser_apiError?.length > 0 && <p className={cls.errorGeneralText}>{updateUser_apiError}</p>}</div>
							</div>
						</div>
					</TabPanel>

				</Tabs>

				{deleteUser && <ModalR
					shadowedContainerStyle={{ maxWidth: '400px', minWidth: '300px', width: '40%' }}
					onClose={() => {
						setDeleteUser(false);
					}}
					isOpen={deleteUser}
					backdropColor={true}
					blured={true}
				>
					<h1 className={cls.deletePosition_title}>{'Delete User'}</h1>
					<p className={cls.deletePosition_description}>{'Are you sure you want to delete this user?'}</p>
					<div className={cls.deleteModalContainer}>
						<div className={cls.deletePosition_buttonContainer} style={{ display: 'flex' }}>
							<Button
								title="Close"
								style={{ marginLeft: '0px', width: '79%', padding: '0 10px' }}
								titleStyle={{ textAlign: 'center' }}
								clicked={() => {
									setDeleteUser(false);
								}}
							/>
							<Button title="Yes, Delete User" style={{ marginLeft: '0px', width: '79%', padding: '0 10px' }} light loading={loading} titleStyle={{ textAlign: 'center' }} clicked={onDeleteUserClick} />
						</div>
					</div>
				</ModalR>}
				{showTasks && data?.user && <UserOverviewModal isVisible={showTasks} activeItem={data?.user} handleClose={() => setShowTasks(false)} isAdmin={user.is_admin} />}


			</div>


		</div>
	);
};
export const LabeledContainer = props => {
	const { label, children, readOnly } = props;
	return (
		<div className={cls.labeledContainerHost} style={{ width: isMobile ? '100%' : '50%' }} app-readonly={readOnly ? 'true' : 'false'}>
			<p className={cls.labeledContainerLabel}>{label}</p>
			{children}
		</div>
	);
};
const LabeledField = props => {
	const { label, error, children, wrappedContent } = props;
	return (
		<div className={cls.labeledFieldHost}>
			<p className={cls.labeledFieldLabel}>{label}</p>
			{wrappedContent ? <div className={cls.labeledFieldChildrenContainer}>{children}</div> : children}

			{error?.length > 0 && <p className={cls.labeledFieldError}>{error}</p>}
		</div>
	);
};

const mapStateToProps = store => {
	const { authenticatedUser } = store.authReducer;
	const { user } = store.sessionReducer;
	const { components, companyData, companyPositionsArray } = store.flowReducer;
	return { authenticatedUser, user, components, companyData, companyPositionsArray };
};

const mapDispatchToProps = dispatch => {
	return {
		onSetAuthenticatedUserPosition: position => dispatch(setAuthenticatedUser(position)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(MyProfile);
